@use '../abstract/' as *;

$grey: grey;
$lighter-grey: rgb(194, 194, 194);

.loading-container {
    @include flex(center, center, row, nowrap, 0.5em);
    background-color: $lighter-grey;
    border-radius: 0.5em;
    height: min(300px, 100%);
    width: min(550px, 100%);
    padding: 2em;
    margin-top: 1em;

    .loading-dot {
        animation: animate-dot 1000ms ease;
        animation-iteration-count: infinite;

        background-color: $grey;
        border-radius: 50%;
        display: inline-block;
        height: 20px;
        width: 20px;
    }

    @for $index from 1 to 5 {
        .loading-dot-#{$index} {
            animation-delay: 100ms * $index;
        }
    }

    @keyframes animate-dot {
        0% {
            opacity: 1;    
            transform: scale(1) translateY(0em);

        } 50% {
            opacity: 0.75;
            transform: scale(1) translateY(-0.5em);

        } 100% {
            opacity: 1;
            transform: scale(1) translateY(0em);
        }
    }
}
